footer {
  background: var(--color-primary);
  padding: 3rem 0;
  text-align: center;
  font-size: 0.9rem;
  margin-top: 7rem;
}
footer a,
footer small {
  color: var(--color-bg);
}
.footer_logo {
  font-size: 2rem;
  font-weight: 500;
  display: inline-block;
  margin-bottom: 2rem;
}
.permaLinks {
  display: flex;
  justify-content: center;
  gap: 2rem;
  font-weight: 450;
  flex-wrap: wrap;
  /* margin: 0 auto; */
}
.footer_socials {
  display: flex;
  justify-content: center;
  gap: 1rem;
  margin-top: 2rem;
  margin-bottom: 4rem;
}
.footer_socials a {
  background: var(--color-bg);
  color: var(--color-white);
  padding: 0.7rem;
  border-radius: 0.7rem;
  border: 1px solid transparent;
  display: flex;
}
.footer_socials a:hover {
  background: transparent;
  color: var(--color-bg);
  border-color: var(--color-bg);
}
footer > small {
  margin-bottom: 4rem;
  display: inline-block;
}
/*============================Media Quries For Small Device=============================*/
@media screen and (max-width: 600px) {
  .permaLinks {
    flex-direction: column;
    gap: 1.5rem;
  }
  .footer_socials {
    margin-bottom: 2.6rem;
  }
}
